"use client"

import { Card } from "@/components/ui/card"
import { useState, useEffect } from "react"

const bannerContent = [
  {
    title: "Kolaborasi untuk Pendidikan Berkualitas",
    description: "Bersama membangun pendidikan yang lebih baik di Kecamatan Cilodong",
    gradient: "from-blue-600 to-blue-700"
  },
  {
    title: "Membangun Masa Depan Bersama",
    description: "Mengembangkan potensi setiap sekolah melalui kerja sama yang solid",
    gradient: "from-blue-700 to-blue-800"
  },
  {
    title: "Inovasi dalam Pendidikan",
    description: "Menerapkan metode pembelajaran modern dan efektif",
    gradient: "from-blue-800 to-blue-900"
  }
]

export default function Banner() {
  const [currentSlide, setCurrentSlide] = useState(0)

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % bannerContent.length)
    }, 5000)
    return () => clearInterval(timer)
  }, [])

  return (
    <div className="relative w-full h-[500px] overflow-hidden">
      {bannerContent.map((content, index) => (
        <div
          key={index}
          className={`absolute inset-0 transition-opacity duration-1000 ${
            currentSlide === index ? "opacity-100" : "opacity-0"
          }`}
        >
          <div className={`w-full h-full bg-gradient-to-r ${content.gradient}`}>
            {/* Decorative pattern */}
            <div className="absolute inset-0 opacity-10">
              <div 
                className="absolute inset-0" 
                style={{
                  backgroundImage: "radial-gradient(circle at 1px 1px, white 1px, transparent 0)",
                  backgroundSize: "40px 40px"
                }}
              />
            </div>
            
            {/* Content */}
            <div className="relative h-full flex items-center justify-center px-4">
              <Card className="max-w-2xl p-6 bg-white/95 backdrop-blur-sm">
                <h1 className="text-3xl md:text-4xl font-bold text-center text-gray-900 mb-4">
                  {content.title}
                </h1>
                <p className="text-center text-gray-700 text-lg">
                  {content.description}
                </p>
              </Card>
            </div>
          </div>
        </div>
      ))}
      
      {/* Slide indicators */}
      <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2 z-30">
        {bannerContent.map((_, index) => (
          <button
            key={index}
            className={`w-2 h-2 rounded-full transition-colors ${
              currentSlide === index ? "bg-white" : "bg-white/50"
            }`}
            onClick={() => setCurrentSlide(index)}
          />
        ))}
      </div>
    </div>
  )
}
